import { FormBuilder } from '@angular/forms';
import { UsersService } from './../services/users.service';
import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';

@Component({
  selector: 'app-find-user',
  templateUrl: './find-user.component.html',
  styleUrls: ['./find-user.component.scss']
})
export class FindUserComponent implements OnInit {
  @ViewChild('mobileNumber') mobileNumber: ElementRef;
  @ViewChild('homeNumber') homeNumber: ElementRef;

  form;

  userData: object;
  userDataReceived: boolean;

  constructor(
    private userService: UsersService,
    fb: FormBuilder
  ) {
    this.form = fb.group({
      address: [''],
      addressRegistered: [''],
      dob: [''],
      email: [''],
      fathersName: [''],
      firstName: [''],
      homeNumber: [''],
      idCode: [''],
      idNumber: [''],
      idSeria: [''],
      lastName: [''],
      phoneNumber: [''],
      region: [''],
      regionRegistered: [''],
      salary: [''],
      workInfo: ['']
    })
  }

  findUser = async (): Promise<any> => {
    let userNumber = {};
    let mobile = this.mobileNumber.nativeElement.value;
    let home = this.homeNumber.nativeElement.value;
    if (mobile !== '') {
      userNumber['mobileNumber'] = mobile;
    }
    if (home !== '') {
      userNumber['homeNumber'] = home;
    }
    let token = window.localStorage.getItem("token");
    return await this.userService.getCreditUser(userNumber, token)
      .then( response => {
        this.userData = response;
        this.userDataReceived = true;
        console.log(this.userData)
      })
      .catch(err => {
        console.log(err)
      })
  }

  ngOnInit(): void {
    
  }

}
