import { Component, OnInit } from "@angular/core";
import { ActivatedRoute } from "@angular/router";
import { RequestsService } from "../services/requests.service";

@Component({
  selector: "app-requests",
  templateUrl: "./requests.component.html",
  styleUrls: ["./requests.component.scss"],
})
export class RequestsComponent implements OnInit {

  requests;
  totalSales: number = 0;
  creditTypes: object[];
  selectedType: number;
  creditID: number;
  constructor(
    private service: RequestsService,
    private route: ActivatedRoute
  ) {}
  
  pageStatus: string = this.route.snapshot.paramMap.get("status");

  status : string = this.pageStatus == 'pending' ? 'Pending' : this.pageStatus == 'declined' ? 'Declined' : this.pageStatus == 'sentForCredit' ? 'Sent For Credit' : this.pageStatus == 'loanApproved' ? 'Loan Approved' : this.pageStatus == 'loanDeclined' ? 'Loan Declined' : undefined;

  public getRequests = async (creditTypeId: object): Promise<any> => {
    let creditStatus: string = this.route.snapshot.paramMap.get("status");
    let token = window.localStorage.getItem("token");
    try {
      this.creditID = creditTypeId['id'];
    } catch(e) {
      this.creditID = null;
    }
    this.service
      .getRequests(creditStatus, token, this.creditID)
      .then((data) => {
        console.log(data)
        if (data !== []) {
          this.requests = data
          data.forEach(element => {
            this.totalSales += Number(element.total)
          });
          console.log(`${this.totalSales} AZN`)
        } else {
          this.requests = []
          this.totalSales = 0
        }
        data !== [] ? (this.requests = data) : this.requests = [];
        // data.requests.noData.length > 0 ? (this.requests = []) : this.requests;
        // data.totalSales > 0 ? (this.totalSales = data.totalSales) : this.totalSales = 0;

      })
      .catch((err) => {
        console.log(err)
        if (err.status === 401) {
          console.error(err.error.unauthorised);
        }
        if (err.status === 400) {
          console.error(err.error.validation);
        }
        if (err.status === 500) {
          console.error(err.error.error);
        }
      });
  };

  private getCreditTypes = async (): Promise<any> => {
    let token = window.localStorage.getItem("token");
    this.service
      .getCreditTypes(token)
      .then((data)=>{
        data.length > 0 ? (this.creditTypes = data) : this.creditTypes;
      })
      .catch((err)=>{
        if (err.status === 401) {
          console.error(err.error.unauthorised);
        }
        if (err.status === 400) {
          console.error(err.error.validation);
        }
        if (err.status === 500) {
          console.error(err.error.error);
        }
      })
  }

  ngOnInit(): void {
    let loggedIn : string = window.localStorage.getItem('loggedIn');
    let expiryTime : string = window.localStorage.getItem('expiry')
    if ( loggedIn === 'false' || loggedIn === '') {
      window.location.href = '/login'
    } else {
      if (JSON.parse(expiryTime) < Date.now()) {
        window.localStorage.setItem('token', '');
        window.localStorage.setItem('expiry', '');
        window.localStorage.setItem('loggedIn', 'false');
        window.location.href = 'login'
      } else {
        this.getRequests(null);
        this.getCreditTypes();
      }
    }
  }
}
