import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { delay } from 'rxjs/operators';

@Injectable({
  providedIn: 'root',
})
export class DataService {

  constructor(private http: HttpClient, private urls: object) {}

  async createRequest(request: object, token: string, type: number) {
    if (token !== '') {
      let headers = { token: `${token}`, id: `${type}` };
      return await this.http
        .post<any>(this.urls['postCreditUrl'], request, { headers })
        .pipe(delay(200))
        .toPromise();
    } else {
      return await Promise.reject('No token sent')
    }
  }

  async getRequests(status: string, token: string, creditTypeId: number) {
    if (token !== '') {
      let headers = { token: `${token}`, status: `${status}` , id : `${creditTypeId}`};
      return await this.http
        .get<any>(this.urls['getCreditsUrl'], { headers })
        .pipe(delay(500))
        .toPromise();
    } else {
      return await Promise.reject('No token sent')
    }
  } 

  async getRequest(requestID: string, token: string) {
    if (token !== '') {
      let headers =  { token: `${token}`, id: `${requestID}`}
      return await this.http
        .get<any>(this.urls['getCreditUrl'], { headers })
        .pipe(delay(200))
        .toPromise();
    } else {
      return await Promise.reject('No token sent')
    }
  }

  async updateRequest(request: object, token: string){
    if (token !== '') {
      let headers = { token: `${token}`}
      console.log(headers)
      console.log(this.urls['updateCreditUrl'])
      console.log(request)
      return await this.http
        .put(this.urls['updateCreditUrl'], request, { headers })
        .pipe()
        .toPromise();
    } else {
      return await Promise.reject('No token sent')
    }
  }

  async updateStatus (requestID: string, status: string, token: string) {
    let body = {
      requestID: requestID,
      status: status
    }
    let headers = { token: `${token}`}
    return await this.http
        .put<any>(this.urls['updateCreditStatusUrl'], body, { headers })
        .pipe(delay(200))
        .toPromise();
  }

  async getCreditTypes (token: string) {
    if (token != '') {
      let headers = { token: `${token}`}
      return await this.http.get<any>(this.urls['getCreditTypesUrl'], { headers })
        .pipe(delay(100))
        .toPromise()
    } else {
      return await Promise.reject('No token sent');
    }
  }
}