import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder } from '@angular/forms';
import { RequestsService } from '../services/requests.service';

@Component({
  selector: 'app-create-request',
  templateUrl: './create-request.component.html',
  styleUrls: ['./create-request.component.scss'],
})
export class CreateRequestComponent implements OnInit {
  form;
  creditTypes : object[] = [
    {
      name: "Rabitebank",
      enum: "1"
    },
    {
      name: "Ferrum",
      enum: "2"
    }
  ]
  creditTypeID: number;

  constructor(fb: FormBuilder, private service: RequestsService) {
    this.form = fb.group({
      firstName: ['', [Validators.required]],
      lastName: ['', [Validators.required]],
      email: ['', [Validators.required, Validators.email]],
      fathersName: ['', [Validators.required]],
      dayOfBirth: ['', [Validators.required, Validators.min(1)]],
      monthOfBirth: ['', [Validators.required, Validators.min(1)]],
      yearOfBirth: ['', [Validators.required, Validators.min(1900)]],
      idSeria: ['', [Validators.required, Validators.minLength(1)]],
      idNumber: ['', [Validators.required, Validators.min(1)]],
      idCode: ['', [Validators.required, Validators.minLength(1)]],
      productId: ['', [Validators.required, Validators.min(1)]],
      productUrl: ['', [Validators.required, Validators.minLength(1)]],
      productTitle: ['', [Validators.required, Validators.minLength(1)]],
      sellerID: ['', [Validators.required, Validators.min(1)]],
      sellerName: ['', [Validators.required, Validators.minLength(1)]],
      categoryID: ['', [Validators.required, Validators.min(1)]],
      categoryName: ['', [Validators.required, Validators.minLength(1)]],
      duration: ['', [Validators.required, Validators.min(1)]],
      upFront: ['', [Validators.required, Validators.min(1)]],
      monthly: ['', [Validators.required, Validators.min(1)]],
      total: ['', [Validators.required, Validators.min(0)]],
      region: ['', [Validators.required, Validators.minLength(1)]],
      address: ['', [Validators.required, Validators.minLength(1)]],
      regionRegistered: ['', [Validators.required, Validators.minLength(1)]],
      addressRegistered: ['', [Validators.required, Validators.minLength(1)]],
      homePhoneNumber: ['', [Validators.required, Validators.minLength(1)]],
      mobilePhoneNumber: ['', [Validators.required, Validators.minLength(1)]],
      contactType: ['', [Validators.required, Validators.minLength(1)]],
      workInfo: ['', [Validators.required, Validators.minLength(1)]],
      occupationInfo: ['', [Validators.required, Validators.minLength(1)]],
      salary: ['', [Validators.required, Validators.minLength(0)]]
    });
  }

  ngOnInit(): void {}

  get firstName(): string {
    return this.form.get('firstName').value;
  }

  get lastName(): string {
    return this.form.get('lastName').value;
  }

  get fathersName(): string {
    return this.form.get('fathersName').value;
  }

  get dayOfBirth(): number {
    return this.form.get('dayOfBirth').value;
  }

  get monthOfBirth(): number {
    return this.form.get('monthOfBirth').value;
  }

  get yearOfBirth(): number {
    return this.form.get('yearOfBirth').value;
  }

  get productId(): number {
    return this.form.get('productId').value;
  }

  get email(): string {
    return this.form.get('email').value;
  }

  get idSeria(): string {
    return this.form.get('idSeria').value;
  }

  get idCode(): string {
    return this.form.get('idCode').value;
  }

  get idNumber(): number {
    return this.form.get('idNumber').value;
  }

  get productUrl(): string {
    return this.form.get('productUrl').value;
  }

  get productTitle(): string {
    return this.form.get('productTitle').value;
  }

  get sellerName(): string {
    return this.form.get('sellerName').value;
  }

  get categoryName(): string {
    return this.form.get('categoryName').value;
  }

  get duration(): number {
    return this.form.get('duration').value;
  }

  get upFront(): number {
    return this.form.get('upFront').value;
  }

  get monthly(): number {
    return this.form.get('monthly').value;
  }

  get total(): number {
    return this.form.get('total').value;
  }

  get region(): string {
    return this.form.get('region').value;
  }

  get address(): string {
    return this.form.get('address').value;
  }

  get regionRegistered(): string {
    return this.form.get('regionRegistered').value;
  }

  get addressRegistered(): string {
    return this.form.get('addressRegistered').value;
  }

  get homePhoneNumber(): string {
    return this.form.get('homePhoneNumber').value;
  }

  get mobilePhoneNumber(): string {
    return this.form.get('mobilePhoneNumber').value;
  }

  get contactType(): string {
    return this.form.get('contactType').value;
  }

  get workInfo(): string {
    return this.form.get('workInfo').value;
  }

  get occupationInfo(): string {
    return this.form.get('occupationInfo').value;
  }

  get salary(): string {
    return this.form.get('salary').value;
  }

  async createCreditRequest() {
    let newRequest: object = {
      firstName: this.firstName,
      lastName: this.lastName,
      email: this.email,
      fathersName: this.fathersName,
      dayOfBirth: this.dayOfBirth,
      monthOfBirth: this.monthOfBirth,
      yearOfBirth: this.yearOfBirth,
      idSeria: this.idSeria,
      idNumber: this.idNumber,
      idCode: this.idCode,
      productID: this.productId,
      productUrl: this.productUrl,
      productTitle: this.productTitle,
      sellerName: this.sellerName,
      categoryName: this.categoryName,
      duration: this.duration,
      upFront: this.upFront,
      monthly: this.monthly,
      total: this.total,
      region: this.region,
      address: this.address,
      regionRegistered: this.regionRegistered,
      addressRegistered: this.addressRegistered,
      homePhoneNumber: this.homePhoneNumber,
      mobilePhoneNumber: this.mobilePhoneNumber,
      contactType: this.contactType,
      workInfo: this.workInfo,
      occupationInfo: this.occupationInfo,
      salary: this.salary
    };
    let token = window.localStorage.getItem('token');
    let typeID = this.creditTypeID
    await this.service
      .createRequest(newRequest, token, typeID)
      .then((response) => {
        alert(response.success);
      })
      .catch((err) => {
        console.error(err);
      });
  }
  setCreditType(type: number) {
    this.creditTypeID = type;
    console.log(this.creditTypeID)
  }
}
