import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class UsersService {
  private loginUrl: string = 'http://165.227.174.162:3000/api/loginUser';
  private logoutUrl: string = 'http://165.227.174.162:3000/api/logoutUser';
  private findUserUrl: string = 'http://165.227.174.162:3000/api/getCreditUser';
  constructor(private http: HttpClient) {}
  
  login = async (creds: object) => {
    return await this.http.post<any>(this.loginUrl, creds).pipe().toPromise();
  }

  logout = async (token: string) => {
    let headers = { 'token' : `${token}`}
    return await this.http.delete<any>(this.logoutUrl, {headers}).pipe().toPromise();
  }

  getCreditUser = async (userNumber: object, token: string) => {
    let headers= {}
    headers = userNumber.hasOwnProperty("mobileNumber") ? {mobileNumber: userNumber['mobileNumber']} : userNumber.hasOwnProperty("homeNumber") ? {homeNumber: userNumber['homeNumber']} : {}
    headers['token'] = token
    console.log(headers)
    return await this.http.get<any>(this.findUserUrl, {headers}).pipe().toPromise();
  }

}
